<template>
    <div class="articllist">
    	<ul >
    		<li class="clearfix"  v-for="item in gridData">
    			<div class="articlimg"><a v-bind:href="item.url" target="_blank"><img v-bind:src="item.thumbnail_pic_s"></a></div>
    			<div class="articlebox">
    				<h3><a v-bind:href="item.url" target="_blank">{{item.title}}</a></h3>
	    			<div class="articledesc"><Tag  type="border" color="blue">{{item.category}}</Tag><span>已发表</span><span>已投自营广告</span><span>{{item.date}}</span></div>
	    			<div class="articleabout">
	    				<span>阅读&nbsp;1245</span><span>评论 &nbsp;20</span><span>点赞 &nbsp;20</span><span>收藏 &nbsp;20</span>
	    				<span>
	    					<Dropdown :transfer="true" @on-click="chatdata">
						        <a href="javascript:void(0)">
						            更多操作
						            <Icon type="arrow-down-b"></Icon>
						        </a>
						        <DropdownMenu slot="list">
						            <DropdownItem name="top">置顶</DropdownItem>
						            <DropdownItem name="editor">修改</DropdownItem>
						            <DropdownItem divided name="delete">删除</DropdownItem>
						        </DropdownMenu>
						    </Dropdown>
	    				</span>
	    			</div>
    			</div>
    		</li>
    	</ul>
    	<div class="loadmore">
    		<Button type="text"  @click="toLoading" v-if="spinShowloadmore">加载更多数据</Button>
    		<Spin fix  v-if="spinShow"><Icon type="load-c" size=12 class="demo-spin-icon-load" style="padding: 0 5px;"></Icon>数据加载中...</Spin>
    	</div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      spinShow: false,
      spinShowloadmore: true,
      gridData: [],
      page: 1
    }
  },
  methods: {
    toLoading () {
      this.spinShow = true
      this.spinShowloadmore = false
      this.getlist()
    },
    chatdata (name) {
      if (name === 'top') {
        this.$Message.warning('置顶操作！!')
      }
      if (name === 'editor') {
        this.$Message.warning('修改操作！!')
      }
      if (name === 'delete') {
        this.$Message.warning('删除操作！!')
      }
    },
    getlist () {
      this.spinShow = true
      this.$http.get('/static/js/data.json?page=' + this.page, {page: this.page}, {emulateJSON: true}).then(
         function (res) {
           for (let i = 0; i <= res.data.result.data.length - 1; i++) {
             this.gridData.push(res.data.result.data[i])
           }
           this.page++
           this.spinShow = false
           this.spinShowloadmore = true
         }, function (res) {
        console.log('111')
      }
     )
    }
  },
  created: function () {
    this.getlist()
  }
}
</script>

<style>
    .demo-spin-icon-load{
        animation: ani-demo-spin 1s linear infinite;
    }
    @keyframes ani-demo-spin {
        from { transform: rotate(0deg);}
        50%  { transform: rotate(180deg);}
        to   { transform: rotate(360deg);}
    }
    .demo-spin-col{
        height: 100px;
        position: relative;
        border: 1px solid #eee;
    }
</style>



